html,
body {
  padding: 0;
  margin: 0;
}

* {
  box-sizing: border-box;
}

.App {
  width: 100vw;
  height: 100vh;
  text-align: center;
  background: linear-gradient(to right, #1e0a0a, #020b33);
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  color: white;
  font-size: 1.5em;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1 , h2 , h3 , h4, h5, h6 , p {
  padding: 6px;
}
